















export default {
	data() {
		return {
			positionTop: 0,
		};
	},
	methods: {
		goPhoneLogin() {
			uni.navigateTo({
				url: '../login-phone/login-phone'
			})
		},
		initPosition() {
			/**
			* 使用 absolute 定位，并且设置 bottom 值进行定位。软键盘弹出时，底部会因为窗口变化而被顶上来。
			* 反向使用 top 进行定位，可以避免此问题。
			*/
			this.positionTop = uni.getSystemInfoSync().windowHeight - 70;
		},
	},
	onLoad() {
		this.initPosition();
	}
}



